.liberty {
	position: relative;
	font-size: 0;
	margin-top: -18px;
	@include r(900) {
		margin-top: 50px; }
	&:before {
		content: "";
		display: block;
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		height: 154px;
		background: url(../img/section4/line.png) no-repeat center;
		background-size: contain;
		@include r(900) {
			display: none; } }
	&__list {
		padding: 112px 24px 0;
		@include r(900) {
			padding: 0; } }
	&__item {
		display: inline-block;
		vertical-align: top;
		width: 50%;
		margin-bottom: 29px;
		padding-left: 26px;
		padding-right: 10px;
		position: relative;
		@include clr;
		@include r(900) {
			display: table;
			width: 100%;
			padding: 0;
			margin: 0;
			&:not(:last-of-type) {
				margin-bottom: 30px; } } }
	&__pic {
		float: left;
		width: 26.74897119341564%;
		min-width: 115px;
		overflow: hidden;
		position: relative;
		@include r(900) {
			width: 115px;
			height: 115px;
			min-width: auto;
			float: none;
			display: table-cell;
			vertical-align: middle; }
		@include r(500) {
			width: 90px;
			height: 90px;
			vertical-align: top; }
		&:after {
			@include rmin(901) {
				content: '';
				display: block;
				padding-top: 100%; } }
		img {
			@include rmin(901) {
				max-width: 100%;
				width: auto;
				top: 50%;
				left: 50%;
				margin-top: -50%;
				margin-left: -50%;
				position: absolute; } } }
	&__content {
		color: #2e2e2e;
		@include fluid-font(24px, 30px, 900px, 1140px, 30px);
		line-height: 1.2;
		padding: 10px 0 10px 5.349794238683128%;
		letter-spacing: -0.01em;
		overflow: hidden;
		@include r(900) {
			display: table-cell;
			vertical-align: middle;
			padding: 0 0 0 36px;
			font-size: 30px; }
		@include r(500) {
			font-size: 22px;
			padding: 0 0 0 15px;
			vertical-align: top; } }
	&__desc {
		color: #959595;
		@include fluid-font(22px, 26px, 900px, 1140px, 26px);
		text-align: center;
		margin-top: -6px;
		line-height: 1.2;
		padding: 0 32px;
		@include r(900) {
			display: none; } }
	.btn {
		display: table;
		margin: 60px auto 0;
		@include r(900) {
			padding: 20px 88px; } } }
