.icon-arrow-next {
 width: 34px;
 height: 64px;
 background-image: url('../img/sprite.png');
 background-position: 0px 0px; }

.icon-arrow-prev {
 width: 34px;
 height: 64px;
 background-image: url('../img/sprite.png');
 background-position: -44px 0px; }
