.present {
	@include clr;
	&__title {
		color: #fff;
		max-width: 905px;
		margin: 0 auto 70px;
		text-align: center;
		@include evo;
		line-height: 1;
		@include fluid-font(38px, 48px, 900px, 1140px, 48px);
		span {
			@include fluid-font(56px, 72px, 900px, 1140px, 72px);
			margin-top: 24px;
			display: block;
			@include r(900) {
				font-size: 72px;
				margin-top: 0; }
			@include r(500) {
				font-size: 36px; } }
		@include r(900) {
			font-size: 48px;
			line-height: 1.2; }
		@include r(500) {
			font-size: 26px;
			margin: 0 auto 40px; } }
	&__col1 {
		width: 45%;
		float: left;
		padding-left: (67 / 1140) * 100 * "1%";
		padding-top: 46px;
		@include r(900) {
			display: none; }
		.advantage {
			margin: 0; }
		.counter {
			margin: 46px 0 0; } }
	&__col1-inner {
		max-width: 390px; }
	&__col2 {
		width: 25%;
		float: left;
		position: relative;
		@include r(900) {
			width: 50%; }
		@include r(650) {
			float: none;
			width: 100%; } }
	&__col3 {
		overflow: hidden;
		position: relative;
		@include r(900) {
			padding-top: 36px; }
		@include r(650) {
			overflow: visible;
			margin-top: 70px; } } }
