.comments {
	max-width: 1093px;
	margin: 0 auto;
	position: relative;
	@include clr;
	&__item {
		position: relative;
		@include r(900) {
			text-align: center; } }
	&__pic {
		@include rmin(901) {
			max-width: 253px;
			position: absolute;
			top: 0;
			z-index: 1;
			&:before {
				content: '';
				display: block;
				position: absolute;
				z-index: -1;
				background: url(../img/section5/mobile__shadow.png) no-repeat center;
				width: (741 / 253) * 100 * "1%";
				padding-top:  (571 / 253) * 100 * "1%";
				opacity: 0.67; }
			&:after {
				content: '';
				display: block; }
			img {
				width: 100%;
				display: block;
				position: absolute; } }
		@include r(900) {
			margin: 0 auto 30px;
			max-width: 253px; }
		@include r(500) {
			img {
				width: 80%;
				margin: auto; } } }
	&__desc {
		position: relative;
		line-height: 1.2;
		@include r(900) {
			user-select: auto; }
		&:before {
			content: '';
			display: block;
			position: absolute;
			width: 121px;
			height: 105px; } }
	&__title {
		color: #272727;
		@include bebas-bold;
		line-height: 1;
		@include fluid-font(36px, 48px, 900px, 1140px, 48px);
		margin-bottom: 41px; }
	&__desc {
		color: #fff;
		@include fluid-font(22px, 26px, 900px, 1140px, 26px); }
	&__item_left {
		@include rmin(901) {
			float: left;
			padding-top: 23px;
			padding-right: 20px;
			padding-bottom: 200px;
			padding-left: ((308 / 1093) * 100) * "1%";
			width: ((806 / 1093) * 100) * "1%";
			.comments__desc {
				&:before {
					background: url(../img/section5/arrow-title-left.png) no-repeat center;
					top: -27px;
					left: -24px; } }
			.comments__pic {
				left: 0;
				width: ((253 / 806) * 100) * "1%";
				&:before {
					top: (227 / 516) * 100 * "1%";
					right: (-237 / 253) * 100 * "1%";
					left: (-251 / 253) * 100 * "1%"; }
				&:after {
					padding-top: ((517 / 253) * 100) * "1%"; } } } }
	&__item_right {
		@include rmin(901) {
			float: right;
			text-align: right;
			margin-top: -271px;
			width: ((798 / 1093) * 100) * "1%";
			padding-top: 150px;
			padding-right: ((276 / 1093) * 100) * "1%";
			padding-bottom: 100px;
			padding-left: 49px;
			.comments__desc {
				text-align: left;
				&:before {
					background: url(../img/section5/arrow-title-right.png) no-repeat center;
					top: -41px;
					right: -13px; } }
			.comments__pic {
				right: 0;
				width: ((253 / 798) * 100) * "1%";
				&:before {
					top: (216 / 516) * 100 * "1%";
					right: (-253 / 253) * 100 * "1%";
					left: (-235 / 253) * 100 * "1%"; }
				&:after {
					padding-top: ((517 / 253) * 100) * "1%"; } } } } }
