.attention {
	@include evo;
	line-height: 1;
	margin: auto;
	padding-top: 15px;
	padding-left: (474 / 1140) * 100 * "1%";
	padding-bottom: 57px;
	position: relative;
	@include r(900) {
		padding: 0; }
	&__pic {
		width: (337 / 1140) * 100 * "1%";
		z-index: 1;
		position: absolute;
		top: 0;
		left: 44px;
		@include r(900) {
			display: none; } }
	&__title {
		@include fluid-font(38px, 48px, 900px, 1140px, 48px);
		color: #2e2e2e;
		@include r(900) {
			font-size: 48px;
			text-align: center;
			line-height: 1.2;
			letter-spacing: -0.02em; }
		@include r(500) {
			font-size: 26px; }
		span {
			@include fluid-font(30px, 36px, 900px, 1140px, 36px);
			display: block;
			@include r(900) {
				font-size: 48px; }
			@include r(500) {
				font-size: 26px; } } }
	&__subtitle {
		@include fluid-font(24px, 30px, 900px, 1140px, 30px);
		color: #ff7200;
		margin-top: 32px;
		@include r(900) {
			display: none; }
		span {
			@include fluid-font(49px, 72px, 900px, 1140px, 72px);
			display: block;
			position: relative;
			margin-bottom: 25px;
			@include rmin(1171) {
				margin-left: -43px; } }
		&_font {
			span {
				@include fluid-font(26px, 32px, 900px, 1140px, 32px);
				margin: 62px 0 0 -32px; } } } }
